import React from 'react'
import { NavLink } from 'react-router-dom';

const Common = (props) => {
  return (
    <>
      <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">
                <div className="col-md-6 mt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>{props.name} <strong className="brand-name">ReactSite</strong></h1>
                  <h2 className="my-3">I am creating my first react website using Bootstrap5</h2>
                  <div className="mt-3">
                    <NavLink to={props.visit} className="btn-get-started">{props.btnName}</NavLink>
                  </div>
                </div>

                <div className="col-md-6 order-1 order-lg-2 header-img">
                  <img src={props.img} alt="animated_img" className="img-fluid animated" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Common;